export const categories = [
  {
    label: 'All',
    id: 'all'
  },
  {
    label: 'Hotel',
    id: 'hotel'
  },
  {
    label: 'Interior',
    id: 'interior'
  },
  {
    label: 'Food',
    id: 'food'
  },
  {
    label: 'Video',
    id: 'video'
  },
  /*
  {
    label: 'Video',
    id: 'video'
  },
  */
  {
    label: 'Others',
    id: 'others'
  }
];

export const isExistId = _id => {
  const category = categories.find(({ id }) => id === _id);
  return !!category?.id;
};

export const idToLabel = _id => {
  const category = categories.find(({ id }) => id === _id);

  if (category?.label) {
    return category?.label;
  } else {
    // 該当するカテゴリーがない場合は all として返す
    return 'All';
  }
};

export const labelToId = _label => {
  const category = categories.find(({ label }) => label === _label);
  return category.id;
};
